import { dispatch, useSelect } from "@wordpress/data";
import { __ } from "@wordpress/i18n";
import { useState, useEffect } from "@wordpress/element";
import { createBlock } from "@wordpress/blocks";
import { isPro, findIndex } from "../helpers";
import { prefix } from "../constants";
import Popup from "reactjs-popup";
import Masonry, { ResponsiveMasonry } from "react-responsive-masonry";

const importerName = __("Import", "wc-booster");
const importingName = __("Importing...", "wc-booster");

const Importer = () => {
	const [open, setOpen] = useState(false);
	const [showLoader, setShowLoader] = useState(true);
	const [currentParentCategory, setCurrentParentCategory] = useState(null);
	const [currentChildCategory, setCurrentChildCategory] = useState(null);
	const [data, setData] = useState([]);
	const [parent, setParent] = useState([]);
	const [demo, setDemo] = useState([]);
	const [isFileMode, setIsFileMode] = useState(false);
	const [isShowingLatest, setIsShowingLatest] = useState(true);
	const [isInserting, setIsInserting] = useState(false);
	const [newTag, setNewTag] = useState(false);
	const [cache, setCache] = useState([]);

	const endPoint = "https://demos.wcbooster.com/wp-json/wp/v2/";

	useEffect(() => {
		const fetchData = async () => {
			try {
				const response = await fetch(
					`${endPoint}demo_category?per_page=100&hide_empty=true&order=asc&orderby=name`,
				);
				const data = await response.json();

				if (data && data.length > 0) {
					const parent = data.filter(
						(node) => node.parent == 0 && node.show_in_rise_blocks == 1,
					);

					const currentParentCategory = parent[0].id;

					setParent(parent);
					setData(data);
					setCurrentParentCategory(currentParentCategory);

					getDemo(currentParentCategory);
				}
			} catch (e) {
				hideLoader();
				console.warn(e.message);
				setIsFileMode(true);
			}
		};

		fetchData();
	}, []);

	const fetchChildMenu = (parentId, e) => {
		e.preventDefault();

		setCurrentParentCategory(parentId);
		setIsShowingLatest(true);

		getDemo(parentId);
	};

	const getDemo = (id = false) => {
		setIsFileMode(false);
		if (!id) {
			id = currentChildCategory;
		}

		if (cache[id]) {
			let data = cache[id];
			if (!isShowingLatest) {
				setCurrentChildCategory(id);
			}
			setDemo(data);
			return;
		}

		setShowLoader(true);
		fetch(
			`${endPoint}single_block_demo?demo_category=${id}&_embed=true&per_page=100&order=desc&orderby=date`,
		)
			.then((response) => response.json())
			.then((res) => {
				if (res) {
					setCache((prevState) => {
						prevState[id] = res;

						return prevState;
					});
					if (!isShowingLatest) {
						setCurrentChildCategory(id);
					}
					setDemo(res);
				}
			})
			.finally(() => {
				hideLoader();
			});
	};

	const onOpen = () => {
		setOpen(true);
		jQuery("body").addClass(`${prefix}-importer-popup-open`);
	};

	const onClose = () => {
		setOpen(false);
		setIsInserting(false);
		jQuery("body").removeClass(`${prefix}-importer-popup-open`);
	};

	const createBlockRecursively = ({ name, attributes, inner = false }) => {
		if (inner) {
			return createBlock(
				name,
				attributes,
				inner.map((node) => createBlockRecursively(node)),
			);
		}
		return createBlock(name, attributes);
	};

	const insertBlock = (json, e) => {
		setIsInserting(true);
		try {
			e.preventDefault();
			for (var i = 0; i < json.length; i++) {
				const data = json[i];
				if (data.name) {
					var created = createBlockRecursively(data);
					dispatch("core/block-editor").insertBlock(created);
				}
			}

			onClose();
		} catch (e) {
			setIsInserting(false);
			console.warn(e.message);
		}
	};

	const favData = useSelect((select) => {
		const { getCurrentUser } = select("core");
		const currentUser = getCurrentUser();
		return currentUser;
	});

	const addToFavourite = (id) => {
		jQuery.post(WBCF.ajax_url, {
			action: "wc_booster_update_user_favourites",
			id,
			security: WBCF.wc_ajax_nonce,
		});
	};

	const importFromFile = (e) => {
		const $input = jQuery(`input[name="${prefix}-json-file"]`);
		const $this = jQuery(e.target);
		const file = $input.prop("files")[0];
		$this.blur();
		if (file) {
			$this.addClass("is-busy").text(importingName);
			const reader = new FileReader();
			reader.onload = () => {
				const data = reader.result;
				try {
					const json = JSON.parse(data);
					insertBlock(json, e);
					$input.val("");
				} catch (e) {
					console.warn(e.message);
				}

				insertBlock(reader.result, e);
				$this.removeClass("is-busy").text(importerName);
			};
			reader.readAsText(file);
		}
	};

	const demos = () => {
		let i = findIndex(data, { id: parseInt(currentParentCategory) });
		if (data.length == 0) {
			return;
		}
		const slug = data[i].slug;

		const isNew = (date) => {
			var today = new Date(),
				date = new Date(date);
			var totalMonth =
				today.getMonth() -
				date.getMonth() +
				12 * (today.getFullYear() - date.getFullYear());
			if (totalMonth <= 1) {
				if (!newTag) {
					setNewTag(true);
				}
				return true;
			}

			return false;
		};

		return (
			<div
				className={`${prefix}-importer-data-box-wrapper ${prefix}-demo-cat-${slug} clearfix`}
			>
				<Masonry>
					{demo.length > 0 &&
						demo.map((node) => {
							let img = "https://placehold.co/400x500";
							if (node._embedded && node._embedded["wp:featuredmedia"]) {
								try {
									let temp =
										node._embedded["wp:featuredmedia"][0].media_details.sizes;
									temp = temp["full"];
									img = temp.source_url;
								} catch (e) {}
							}

							let preview =
								node.preview_link == "" ? node.link : node.preview_link;

							return (
								<div
									key={node.id}
									className={`${prefix}-importer-data-box-inner-wrapper`}
								>
									<div className={`${prefix}-importer-data-box`}>
										{isNew(node.date) && (
											<div className={`${prefix}-new-block`}>
												<span>{__("New", "wc-booster")}</span>
											</div>
										)}
										<div className={`${prefix}-importer-data-box-inner`}>
											{img && (
												<img
													src={img}
													alt=""
													loading="lazy"
													style={{ display: "block", width: "100%" }}
												/>
											)}
											<a href={preview} target="_blank">
												<span className="dashicons dashicons-visibility"></span>
											</a>
										</div>
										<div className={`${prefix}-importer-data-box-footer`}>
											{node.hide_title == "" && <h3>{node.title.rendered}</h3>}
											{insertButton(node)}
										</div>
									</div>
								</div>
							);
						})}
				</Masonry>
			</div>
		);
	};

	const insertButton = (node) => {
		if (node.is_pro == "" || (node.is_pro == "1" && isPro())) {
			return (
				<>
					<a
						href="#"
						className={`${prefix}-insert-button`}
						onClick={(e) => {
							if (!isInserting) {
								insertBlock(node.demo_json, e);
							}
						}}
					>
						<span className="dashicons dashicons-download"></span>
						{__("Insert ", "wc-booster")}
					</a>

					<a
						href="#"
						className={`${prefix}-favourite-button ${
							favData &&
							Object.values(favData.wc_booster_favourites_demo).includes(
								node.id.toString(),
							)
								? "favourite"
								: ""
						}`}
						onClick={(e) => {
							const $this = jQuery(e.currentTarget);
							if ($this.hasClass("favourite")) {
								$this.removeClass("favourite");
							} else {
								$this.addClass("favourite");
							}
							addToFavourite(node.id);
						}}
					>
						<span className="dashicons dashicons-heart"></span>
					</a>
				</>
			);
		}
		return (
			<a
				href="https://wcbooster.com/pricing/"
				target="_blank"
				className={`${prefix}-get-pro`}
			>
				{__("Get Pro ", "wc-booster")}
				<span className="dashicons dashicons-arrow-right-alt"></span>
			</a>
		);
	};

	const hideLoader = () => {
		setShowLoader(false);
	};

	return (
		<div className={`${prefix}-importer-popup-button`}>
			<button
				onClick={onOpen}
				className={`is-button components-button is-primary is-large ${prefix}-importer-trigger`}
			>
				<span className="dashicons dashicons-upload"></span>
				{__("WC Booster Demo Importer", "wc-booster")}
				{newTag && (
					<span className={`${prefix}-new-tag`}>{__("New", "wc-booster")}</span>
				)}
			</button>
			<Popup open={open} closeOnDocumentClick onClose={onClose}>
				<div className="modal wc-booster-modal">
					<a className="close" onClick={onClose}>
						&times;
					</a>
					<div className="content">
						<div className={`${prefix}-importer-popup-header`}>
							<div className={`${prefix}-importer-popup-title`}>
								<h2>{__("Library", "wc-booster")}</h2>
							</div>

							<div className={`${prefix}-importer-popup-menu`}>
								<ul>
									{parent.map((node) => {
										return (
											<li key={node.id}>
												<a
													href="#"
													className={
														!isFileMode && currentParentCategory == node.id
															? "active-menu"
															: ""
													}
													onClick={(e) => fetchChildMenu(node.id, e)}
												>
													<span
														className={`dashicons dashicons-${node.icon_class}`}
													></span>
													{node.name}
												</a>
											</li>
										);
									})}
									<li>
										<a
											href="#"
											className={isFileMode ? "active-menu" : ""}
											onClick={(e) => {
												e.preventDefault();
												hideLoader();
												setIsFileMode(true);
											}}
										>
											<span className="dashicons dashicons-media-spreadsheet"></span>
											{__("File", "wc-booster")}
										</a>
									</li>
								</ul>
							</div>
						</div>

						{!isFileMode && !showLoader && demos()}

						{isFileMode && (
							<div className={`${prefix}-importer-data-box-wrapper`}>
								<div className={`${prefix}-import-popup-file`}>
									<input type="file" name={`${prefix}-json-file`} />
									<button
										onClick={importFromFile}
										className={`button components-button button-primary button-large ${prefix}-importer-action`}
									>
										{importerName}
									</button>
								</div>
							</div>
						)}

						{showLoader && (
							<div className={`${prefix}-importer-data-box-wrapper`}>
								<div className={`${prefix}-loader-wrapper`}>
									<div className={`${prefix}-loader-container`}>
										<div
											className={`${prefix}-loader-item wc-booster-loader-item-1`}
										></div>
										<div
											className={`${prefix}-loader-item wc-booster-loader-item-2`}
										></div>
										<div
											className={`${prefix}-loader-item wc-booster-loader-item-3`}
										></div>
										<div
											className={`${prefix}-loader-item wc-booster-loader-item-4`}
										></div>
									</div>
								</div>
							</div>
						)}
					</div>
				</div>
			</Popup>
		</div>
	);
};

export default Importer;
